}
@font-face {
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url(/RoboFriends/static/media/SEGA.12a3db8b.woff) format('woff');
    }

h1 {
    font-family: 'SEGA LOGO FONT';
    font-weight: 200;
    color: #0ccac4;
}
.back {
    position: fixed;
    padding: 0;
    margin: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: white;
    -webkit-animation-name: backdiv;
            animation-name: backdiv;
    -webkit-animation-duration: 1s;
            animation-duration: 1s; 
    -webkit-animation-iteration-count:infinite; 
            animation-iteration-count:infinite;
  }
  
  .cube {
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: lightblue;
    height: 50px;
    width: 50px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    -webkit-animation-name: beat;
            animation-name: beat;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count:infinite;
            animation-iteration-count:infinite;
  }
  @-webkit-keyframes backdiv {
    50% {
      background: #71b5cc;
    }
  }
  @keyframes backdiv {
    50% {
      background: #71b5cc;
    }
  }
  
  @-webkit-keyframes beat {
    0% {
      -webkit-transform: scale(1) rotate(-45deg);
              transform: scale(1) rotate(-45deg);
    }
    50% {
      -webkit-transform: scale(0.6) rotate(45deg);
              transform: scale(0.6) rotate(45deg);
    }
    100% {
      -webkit-transform:scale(1) rotate(45deg);
              transform:scale(1) rotate(45deg);
    }
  }
  
  @keyframes beat {
    0% {
      -webkit-transform: scale(1) rotate(-45deg);
              transform: scale(1) rotate(-45deg);
    }
    50% {
      -webkit-transform: scale(0.6) rotate(45deg);
              transform: scale(0.6) rotate(45deg);
    }
    100% {
      -webkit-transform:scale(1) rotate(45deg);
              transform:scale(1) rotate(45deg);
    }
  }
